import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { Map, GoogleApiWrapper, Marker } from 'google-maps-react';

import LocationInput from './components/LocationInput';
import ContainerCoords from './components/ContainerCoords';
import Modal from './components/Modal';
import MarcadorEdit from './components/MarcadorEdit';

import * as CONST from './constants';
import * as Utils from './utils';

const FullMap = styled(Map)`
	width: 100vw;
	height: 100vh;
`;

// const ContainerButtons = styled.div`
// 	display: grid;
// 	grid-template-columns: 1fr 1fr;
// `;

const ContainerLocationInput = styled.div`
	position: absolute;
	left: 50%;
	width: 600px;
	margin-left: -300px;
	top: 20px;
`;

// const Button = styled.button`
// 	width: 100%;
// 	border-bottom-left-radius: 2px;
// 	border-bottom-right-radius: 2px;
// 	padding: 10px 0;
// 	bottom: 10px;
// 	background-color: #fff;
// 	border: 1px;
// 	border: 1px solid #888;
// 	transition: all 0.5s;
// 	font-family: 'Roboto';
// 	font-weight: 900;

// 	&:hover {
// 		background-color: #f2f2f2;
// 	}
// `;

function App(props) {
	const [editMarcador, setEditMarcador] = useState({ open: false, item: {} });
	const [markers, setMarkers] = useState([]);
	// const [route, setRoute] = useState([]);
	const [centralizarMapa, setCentralizarMapa] = useState({ points: [], centralizar: false });
	const [mapCenter, setMapCenter] = useState({
		center: { lat: -12.579738, lng: -41.70072720000002 },
		zoom: 7,
		bounds: undefined,
	});

	window.globalDelphiSetMarkers = setMarkers;

	const handleMapClick = (a, b, c) => {
		const obj = { ...c };
		const cord = { ...obj.latLng };
		setMarkers(prevState => [
			...prevState,
			{ lat: cord.lat(), lng: cord.lng(), address: `Marcador ${prevState.length + 1}` },
		]);
	};
	const handlePlaceSelected = location => {
		setMarkers(markers => {
			const newMarkers = [...markers, location];
			setCentralizarMapa({ points: newMarkers, centralizar: true });
			return newMarkers;
		});
	};
	const handleItemClick = index => setCentralizarMapa({ points: [markers[index]], centralizar: true });
	const handleDelete = index => setMarkers(markers => markers.filter((value, i) => i !== index));
	const handleReorder = coords => setMarkers(coords);
	// const handleSalvar = () => {
	// 	var a = document.createElement('a');
	// 	var file = new Blob([JSON.stringify(markers)], { type: 'application/json' });
	// 	a.href = URL.createObjectURL(file);
	// 	a.download = 'coordenadas';
	// 	a.click();
	// };
	// const handleProcessar = () => {
	// 	if (markers.length < 2 || markers.length > 25) {
	// 		alert(`Número de pontos inválido. (Máximo = 25)`);
	// 		return;
	// 	}

	// 	const waypoints = markers
	// 		.filter((coord, index) => index !== 0 && index !== markers.length - 1)
	// 		.map(coord => ({ location: `${coord.lat},${coord.lng}`, stopover: true }));

	// 	const params = {
	// 		origin: `${markers[0].lat},${markers[0].lng}`,
	// 		destination: `${markers[markers.length - 1].lat},${markers[markers.length - 1].lng}`,
	// 		travelMode: 'DRIVING',
	// 		waypoints: waypoints,
	// 	};

	// 	var directionsService = new props.google.maps.DirectionsService();
	// 	directionsService.route(params, (response, status) => {
	// 		if (status === 'OK') {
	// 			const path = props.google.maps.geometry.encoding.decodePath(response.routes[0].overview_polyline);
	// 			setRoute(path);
	// 		} else {
	// 			window.alert('Falha ao traçar rota: ' + status);
	// 		}
	// 	});
	// };
	const handleConfirmEditMarcador = item => {
		setMarkers(markers.map((value, index) => (index === item.index ? item : value)));
		setEditMarcador({ open: false, item: {} });
	};

	useEffect(() => {
		const { points, centralizar } = centralizarMapa;
		if (points.length > 0 && centralizar) {
			var bounds = new props.google.maps.LatLngBounds();
			for (var i = 0; i < points.length; i++) {
				bounds.extend(points[i]);
			}
			setMapCenter({ center: bounds.getCenter(), bounds: bounds });
		}
	}, [props, centralizarMapa]);

	return (
		<>
			<input type='hidden' id='globalDelphiMarkers' value={JSON.stringify(markers)} />
			<FullMap
				minZoom={3}
				maxZoom={18}
				onClick={handleMapClick}
				bounds={mapCenter.bounds}
				google={props.google}
				initialCenter={mapCenter.center}
				center={mapCenter.center}
				zoom={mapCenter.zoom}
			>
				{/* route.length > 0 && (
					<Polyline path={route} strokeColor="#3680ff" strokeOpacity={0.6} strokeWeight={4} />
				)*/ }

				{markers.map((location, index) => (
					<Marker
						key={index}
						label={{
							text: Utils.getLetra(index + 1),
							fontFamily: 'Roboto',
							fontSize: '12px',
							color: '#fff',
							fontWeight: '900',
						}}
						position={location}
					/>
				))}

				<ContainerLocationInput>
					<LocationInput onPlaceSelected={handlePlaceSelected} />
				</ContainerLocationInput>

				<ContainerCoords
					onEdit={index => setEditMarcador({ open: true, item: { ...markers[index], index: index } })}
					onItemClick={handleItemClick}
					coords={markers}
					onDelete={handleDelete}
					onReorder={handleReorder}
				>
					{/*<ContainerButtons>
						<Button
							style={{ borderRight: 'none', borderBottomRightRadius: '0' }}
							disabled={markers.length === 0}
							onClick={() => handleSalvar()}
						>
							SALVAR
						</Button>
						<Button
							style={{ borderBottomLeftRadius: '0' }}
							disabled={markers.length < 2 || markers.length > 25}
							onClick={handleProcessar}
						>
							PROCESSAR ROTA
						</Button>
					</ContainerButtons>*/}
				</ContainerCoords>

				<Modal
					closeOnOverlayClick
					onClose={() => setEditMarcador({ open: false, item: {} })}
					open={editMarcador.open}
				>
					{editMarcador.open && (
						<MarcadorEdit
							onConfirm={handleConfirmEditMarcador}
							onCancel={() => setEditMarcador({ open: false, item: {} })}
							item={editMarcador.item}
						/>
					)}
				</Modal>
			</FullMap>
		</>
	);
}

export default GoogleApiWrapper({
	apiKey: CONST.API_KEY,
	language: 'pt-BR',
})(App);
